<template>
    <div id="app" class="user-select-none">
        <router-view class="container" v-if="$store.state.monster_active && $store.state.monster_data && $store.state.monster_team && $store.state.monster_leader && $store.state.seal_data && $store.state.main_data" />   
    </div>
</template>
<script>
export default {
    name:"App",
    created(){
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/MONSTER_DATA.json').then((res) => {
            this.$store.commit('data', res.data)
        })
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/MONSTER_ACTIVE.json').then((res) => {
            this.$store.commit('active', res.data)
        })
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/MONSTER_LEADER.json').then((res) => {
            this.$store.commit('leader', res.data)
        })
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/MONSTER_TEAM.json').then((res) => {
            this.$store.commit('team', res.data)
        })
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/SEAL_DATA.json').then((res) => {
            this.$store.commit('seal', res.data)
        })
        this.$http.get('https://raw.githubusercontent.com/andyjjrt/tospackdata/master/data/data.json').then((res) => {
            this.$store.commit('main_data', res.data)
        })
    }
}
</script>
