import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    monster_data: null,
    monster_active: null,
    monster_leader: null,
    monster_team: null,
    seal_data: null,
    main_data: null
  },
  mutations: {
    data(state, payload){
      state.monster_data = payload
    },
    active(state, payload){
      state.monster_active = payload
    },
    leader(state, payload){
      state.monster_leader = payload
    },
    team(state, payload){
      state.monster_team = payload
    },
    seal(state, payload){
      state.seal_data = payload
    },
    main_data(state, payload){
      state.main_data = payload
    }
  },
})
